body { background-color: #c7e9fb;
    background-image: 
    linear-gradient(315deg, #c7e9fb 10%, #e61d8c 80%);
    background-attachment: fixed;
    margin: 0; 
    padding-bottom: 3rem; 
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; }

form { background: rgba(0, 0, 0, 0.15); 
    padding: 0.25rem; 
    position: fixed; 
    bottom: 0; 
    left: 0; 
    right: 0; 
    display: flex; 
    height: 3rem; 
    box-sizing: border-box; 
    backdrop-filter: blur(10px); 
}

input { border: none; 
    padding: 0 1rem; 
    flex-grow: 1; 
    border-radius: 2rem; 
    margin: 0.25rem; 
}

input:focus { outline: none; }

form > button { background: rgb(230, 33, 124); 
    border: none; 
    padding: 0 1rem; 
    margin: 0.25rem; 
    border-radius: 3px; 
    outline: none; 
    color: #fff; 
}

#messages { 
    list-style-type: none; 
    margin: 10px; 
    padding: 0; 
}

#messages > li { 
    opacity: 0.5;
    background-color: #c7e9fb; 
    width: auto;
    min-height:50px; 
    font-size: 20px;
    border: 1px solid #fff;
    border-radius: 10px; 
    padding-bottom: 5px;
    margin: 0 0 8px 0;
    text-align: left;
    padding-left: 15px;
    left: 5%; 
}

#messages > li:nth-child(odd) { 
    background: #efefef; 
    text-align: right; 
    left: 250px; 
    padding-right: 15px; 
    padding-left: 5px;
}